html{
	min-height: 100%;
    background-color:#F98866;
    /*background-image: linear-gradient(315deg, #65faff 0%, #0d0e0e 74%);*/

}
body{
	margin:0;
	border: 0;
	min-height: 100vh;
}
#root{
	min-height: 100vh;
	width: 100%;
}
.homeContainer{
    height: 100vh;
    width: 100vhs;
    color:#FFF2D7;
    display: flex;
    flex-direction: column;
}
.ActionForm{
    height:100%;
    width:100%;
    display: flex;
    flex-direction: column;
    justify-items: center;
}
.ActionForm button{
    margin:10vh 0;
    width: 150px;
    height: 40px;
}
.button-class{
    display: flex;
    width:100%;
    justify-content: center;
}
button
{
    border:0;
    margin:0;
    font-size:1.5rem;
    color:black;
    border-radius:10px;
    height:30px;
    background-color:#85d1f7;
}
.collectMap{
    background-color:#FFF2D7;
}
.h2baad{
    color:white;
}
.splitInput{
    margin: 20vh 0;
    background-color: #FFF2D7;
}
.textBoxInp{
    margin-top: 1vh;
}
.LoadingDiv{
    height: 80vh;
    display: flex;
    justify-content: center;
    align-items: center;
}
.nameHeading{
    color:rgb(61, 16, 16);
    text-align: center;
}
.changeUserCont{
    display: flex;
    flex-direction: column;
    align-items: flex-end;
}
.changeUserCont button{
    margin-right: 5vh;
}